		font-size: 68px;
		margin-right: 20px;
	}
	.choiceTitleImg>div{
		font-size: 22px;
		display: inline-block;
		vertical-align: top;
		line-height: 78px;
		height: 100%;

	}
	.TitleInput{
		width: 938px;
	    height: 47px;
	    border-radius: 5px;
	    background-color: rgb(233, 233, 233);
	    box-shadow: 0px 1px 6px 0px rgba(255, 255, 255, 0.57);
	    outline: none;
	    border: none;
	    font-size: 18px;
	    padding-left: 39px;
	    display: block;
	    margin: 0 auto;
	    margin-top: 28px;
	}
	/**/
	/**/
	.compDataL,
	.compDataR
	{
		display: inline-block;
		vertical-align: top;
	}
	.PushCompContent{
		width: 870px;
		margin: 0 auto;
	}
	.PushCompanionBtn{
		width: 102px;
		height: 36px;
		background-color: #7086c6;
	    border-radius: 3px;
	    color: #fff;
	    
	    margin: 0 auto;
		margin-top: 30px;
		margin-bottom: 48px;
	    line-height: 36px;
	    letter-spacing: 1px;
	}
	.compDataL{
		width: 476px;
		margin-top: 10px;
	}
	.compDataR{
		margin-top: 24px;
		margin-left: 69px;
	}
	.startAdd>div,
	.endAdd>div,
	.comDateText>div,
	.compDataR>div,
	.comDate>div,
	.companionInfo>div{
		display: inline-block;
		vertical-align: top;
		height: 32px;
		line-height: 32px; 
	}
	.pushCodeImg{
		
	   
	    background-color: #768bc8;
	    color: #fff;
	    margin-top: 20px;

	  
	}
	.cancelCodeImg{
	   
	    background-color: #e9e9e9;
	    color: #747474;
	    margin-top: 15px;
	}
	.pushCodeImg,
	.cancelCodeImg{
		line-height: 26px;
		height: 26px;
		width: 83px;
		border-radius: 3px;
		font-size: 13px;

	}
	.PushCompanionBtn,
	.pushCodeImg,
	.cancelCodeImg,
	.province,
	.city
	{
		 cursor: pointer;
		 text-align: center;
	}
	
	.userDode{
