<template>
	<div class="perreplay">
		<p><span>{{ replayLen }}</span></p>
		<div class="replayBox">
			<div class="replayItem_1" v-for="item in dataList" :promptid= "item.prompt_id">
				<p v-show="item.type == '1'">
					<span>{{ item.from_who }}</span>  <span @click="viewDetail"></span>
				</p>
				<p v-show="item.type == '2'">
					1 <span>{{ item.from_who }}</span>  <router-link :to="'/strategydetail?id='+item.strategy_id"  @click.native="read(item.prompt_id)"></router-link>
				</p>
				<p v-show="item.type == '3'">
					1 <span> {{ item.from_who }} </span> <router-link :to="'/strategydetail?id='+item.strategy_id" @click.native="read(item.prompt_id)"></router-link>
				</p>
				<!--  <span></span> 1  <span>K</span>  -->
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default {
		data () {
			return {
				replayLen: 0,
				dataList: ''
			}
		},
		methods: {
			viewDetail (e) {
				let id = e.currentTarget.parentNode.parentNode.getAttribute
				this.read(id)

				this.$emit('ifchange',true)
			},
			read (id) {
				this.$http.post('https://wy.lujiahaoo.cn/tour/published.php',{
					"action":"prompt_read",
					"prompt_id": id
				},{}).then(result => {
					console.log(result)
				})
			}
		},
		mounted () {
			this.$http.post('https://wy.lujiahaoo.cn/tour/published.php',{
				"action":"prompt_list",
			},{}).then(result => {
				console.log(result)
				
				if (result.body.code == '1') {
					if (result.body.data) {
						this.replayLen = result.body.data.length
						this.dataList = result.body.data
					}
					
				}else {
					this.replayLen = 0
				}
			})
		}
	}
</script>
<style type="text/css">
	.replayBox{
		width: 715px;
		min-height: 449px;
		border:1px solid #ccc;
		padding: 20px;
		margin-bottom: 20px;
	}
	.perreplay{
		width: 756px;
		margin: 0 auto;
	}
	.perreplay>p{
		margin-bottom: 20px;
	}
	.replayItem_1>span,
	.replayItem_2>span{
		color: #7086c6;
		cursor: pointer;
	}
	.replayItem_1,
	.replayItem_2{
		height: 40px;
		line-height: 40px;
		display: inline-block;
		border-bottom:1px solid #000;
	}
</style>